.how-it-works {
  margin-bottom: 48px;
  padding: 0 20px;
}

.section-heading {
  font-size: 32px;
  line-height: 32px;
  font-weight: 500;
  margin-bottom: 48px;
  text-align: center;
}

.steps {
  display: flex;
  gap: 48px;
  justify-content: center;
  align-items: center;
}

.step-icon {
  margin-bottom: 24px;
}

.step-name {
  font-size: 24px;
  line-height: 32px;
  font-weight: 500;
  margin-bottom: 16px;
  text-align: center;
}

.step-description {
  line-height: 26px;
  text-align: center;
}

.arrow-wrapper {
  width: 48px;
  height: 48px;
  border-radius: 100px;
  background-color: #f7f7f7;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cta {
  padding: 0 20px;
}

.cta-wrapper {
  background: url("../../icons/HPMid/cta-bg.png");
  background-position: top 0 left -3px;
  background-color: #7238ed;
  width: 100%;
  max-width: 1280px;
  height: 164px;
  margin: 0 auto;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 100px;
  margin-bottom: 128px;
}

.cta-text {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.cta-heading {
  font-size: 24px;
  line-height: 32px;
  font-weight: 500;

  color: #fff;
}
.cta-subheading {
  line-height: 24px;

  color: rgba(255, 255, 255, 0.8);
}

.cta-button {
  height: 44px;
  width: 193px;
  background-color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-family: inherit;
  cursor: pointer;
}

.reviews {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

.reviews-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 57px;
  & .section-heading {
    margin-bottom: 0;
  }
}

.reviews-buttons {
  display: flex;
  gap: 16px;
  cursor: pointer;
}

.btn-review {
  width: 48px;
  height: 48px;
  border-radius: 100px;
  border: none;
  background-color: #f6f9fc;
}

.review-cards {
  // width: 100%;
  display: flex;
  justify-content: space-between;
}

.geography {
  background: url("../../icons/HPMid/geographyBg.jpg");
  width: 100%;
  max-width: 1440px;
  height: 464px;
  padding-top: 64px;
  margin-top: 119px;
  & .section-heading {
    margin-bottom: 48px;
  }
}

.cities {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, auto);
  row-gap: 20px;
  column-gap: 70px;
  justify-content: center;
}

.city {
  line-height: 26px;
}
