.left {
  min-width: 30%;
  max-width: 60%;
  // max-width: 540px;
  flex-grow: 1;
  position: relative;
  min-height: 420px;
  margin-top: 150px;
}

.left-content {
  position: relative;
  display: flex;
  flex-direction: column;
  position: relative;
}

.ads-count-label {
  font-size: 20px;
  line-height: 26px;
  color: #7b7b7b;
}
.ads-count-number {
  font-size: 35px;
  line-height: 64px;
  font-weight: bold;
}
.heading {
  margin-top: 64px;
  margin-bottom: 32px;
  font-size: 64px;
  line-height: 64px;
  width: 100%;
  max-width: 531px;
}

.subheading {
  display: block;
  font-size: 20px;
  line-height: 26px;
  width: 100%;
  max-width: 531px;
  margin-bottom: 54px;
}

.img-block {
  position: relative;
  width: fit-content;
  // margin-top: 150px;
  height: fit-content;

  // max-width: 502px;
  // max-height: 697px;
  // min-height: 500px;
}
