.people {
  height: 35vw;

  max-height: 502px;
  min-height: 280px;
  // margin-top: 100px;
}

.woman {
  position: absolute;
  top: 0;
  left: 0;

  max-height: 279px;
}
.hands {
  position: absolute;

  top: 209px;
  left: 291px;
  // height: 18vw;
  max-width: 205px;
}
.man {
  position: absolute;
  top: 297px;
  left: 69px;
}

.promo {
  width: 294px;
  height: 204px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  position: absolute;
  left: 44%;
  top: -63px;
  // left: 219px;
  // top: -63px;
}

.promo-row-1 {
  display: flex;
  gap: 10px;
}
.promo-row-2 {
  display: flex;
  gap: 16px;
  flex-grow: 1;
}

.promo-box-1 {
  width: 57px;
  height: 16px;

  background-color: #f2f2f2;
  border-radius: 4px;
}
.promo-box-2 {
  width: 115px;
  height: 16px;
  background-color: #f2f2f2;
  border-radius: 4px;
}
.promo-data-block-1 {
  max-width: 119px;
  max-height: 109px;
  padding: 12px;
  display: flex;
  flex-direction: column;

  background-color: #fff;
  box-shadow: 0px 45px 104px rgba(0, 0, 0, 0.06);
  border-radius: 8px;
}
.promo-data-block-2 {
  align-self: flex-end;

  width: 159px;
  height: 109px;
  padding: 12px;
  display: flex;
  flex-direction: column;

  background-color: #fff;
  box-shadow: 0px 45px 104px rgba(0, 0, 0, 0.06);
  border-radius: 8px;
}

.promo-label {
  font-size: 14px;
  margin-bottom: 7px;
  white-space: nowrap;
}
.promo-values {
  display: flex;
  flex-direction: column;
}
.promo-value {
  font-size: 20px;
  font-weight: 600;
}

.circle {
  align-self: flex-start;
  margin-bottom: 20px;
}

.search {
  position: absolute;
  top: 359px;
}

.search-input {
  background-color: #000;
  color: #fff;
  height: 58px;
  width: 324px;
  padding-left: 90px;
  font-size: 20px;
  border: none;
  font-family: inherit;
  border-radius: 8px 8px 8px 0;
  &::placeholder {
    color: #fff;
  }
}
.search-icon {
  position: absolute;
  left: 54px;
  top: 50%;
  transform: translateY(-50%);
}

.btn {
  position: absolute;
  top: 586px;
  right: 57px;
  height: 58px;
  width: 324px;
  background-color: #f2f2f2;
  padding-left: 52px;
  font-size: 20px;
  display: flex;
  align-items: center;
  gap: 16px;
  border: none;
  font-family: inherit;
  border-radius: 8px 8px 0 8px;
}

