@media (max-width: 1280px) {
  .page {
    padding-left: 40px;
    padding-top: 0;
  }
  .main {
    margin-top: 60px;
  }
  .left {
    margin-top: 0;
  }
  // .img-block {
  //   margin-top: 90px;
  // }

  .heading {
    font-size: 32px;
    line-height: 32px;

    margin-top: 30px;
  }
  .promo {
    width: 100px;
  }
  .promo-data-block-1 {
    width: 90px;
    padding: 6px;
  }
  .promo-data-block-2 {
    padding: 6px;
    width: 100px;
  }

  .promo-label {
    font-size: 12px;
  }
  .promo-value {
    font-size: 16px;
  }
  // .main {
  //   &::after {
  //     background-position: left 100px top 200px;
  //   }
  // }
}
@media (max-width: 1100px) {
  .background {
    background-position: left 100px top 100px;
  }
}

@media (max-width: 900px) {
  .heading {
    font-size: 48px;
    line-height: 48px;
  }
}
@media (max-width: 730px) {
  .heading {
    font-size: 32px;
    line-height: 32px;
    margin-top: 30px;
  }
}
@media (max-width: 818px) {
  .main {
    flex-direction: column;
    gap: 90px;
    padding-right: 0;
    margin-bottom: 40px;
  }
  .left {
    min-height: 0;
    width: 100%;

    // height: 50vh;
    max-width: none;
  }
  .heading {
    font-size: 48px;
    line-height: 48px;

    margin-top: 30px;
  }
  .subheading {
    margin-bottom: 24px;
  }
}
.background {
  background-position: center;
  background-size: cover;
}
.img-block {
  align-self: center;
}

@media (max-width: 600px) {
  .page {
    padding-left: 0;
  }

  .ads-count-label {
    font-size: 16px;
  }

  .ads-count-number {
    font-size: 32px;
  }

  .left-content {
    padding-left: 24px;
    padding-right: 24px;
  }
  .heading {
    font-size: 32px;
    line-height: 32px;
  }
  .subheading {
    font-size: 16px;
    line-height: 22px;
  }
}

@media (max-width: 400px) {
  .img-block {
    width: 100%;
    padding-left: 6px;
    // left: 0;
    // align-items: start;
  }

  .promo {
    left: 30%;
  }
}

@media (max-width: 345px) {
  .promo-row-2 {
    gap: 0px;
  }
  .left-content {
    padding: 0 6px;
  }
}

