.page {
  padding: 64px 40px;
  background-color: #f6f9fc;
}

.container {
  max-width: 1240px;
  margin: 0 auto;
}

.heading {
  font-size: 32px;
  line-height: 32px;
  font-weight: 500;
  width: 344px;
  margin-bottom: 40px;
}

.underline {
  border-top: 1px solid #eae8ef;
  display: flex;
  justify-content: space-between;
  width: 100%;

  padding-top: 26px;
  margin-bottom: 48px;
}

.subheading {
  color: #8f8f8f;
  width: 531px;
  font-size: 20px;
  line-height: 26px;
}

.help-block {
  display: flex;
  gap: 28px;
}

.help-text {
  color: #8f8f8f;

  font-size: 20px;
  line-height: 26px;
  text-align: right;
}

.btn-help {
  background-color: #7238ed;
  color: #fff;
  height: 54px;
  width: 187px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 20px;
  font-family: inherit;
}
.cards {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(4, auto);
  // column-gap: 40px;
  justify-content: space-between;
  row-gap: 32px;
  margin-bottom: 26px;
}

.card {
  width: 280px;
  padding: 24px 20px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 8px;
}

.icon {
  align-self: flex-start;
}

.name {
  font-size: 24px;
  line-height: 24px;
  font-weight: 600;
  font-family: "Rubik";
  margin: 24px 0;
}

.list {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-bottom: 16px;
}

.option {
  display: flex;
  justify-content: space-between;
  line-height: 26px;
}

.count {
  color: #8f8f8f;
}

.btn-all {
  width: 58px;
  border: none;
  font-size: 16px;
  font-weight: 500;
  font-family: inherit;
  background-color: transparent;
  cursor: pointer;
  white-space: nowrap;
}

@media (max-width: 1368px) {
  .card {
    width: 370px;
  }
  .cards {
    grid-template-columns: repeat(3, auto);
  }
}
@media (max-width: 1165px) {
  .card {
    width: 300px;
  }
}
@media (max-width: 1032px) {
  .cards {
    grid-template-columns: repeat(2, auto);
    justify-content: center;
    column-gap: 22px;
    row-gap: 16px;
  }
  .help-block {
    width: 100%;
    justify-content: space-between;
    justify-self: center;
  }
}
@media (max-width: 650px) {
  .page {
    padding: 24px;
  }

  .cards {
    grid-template-columns: repeat(1, auto);
  }
  .card {
    width: 327px;
  }
  .help-block {
    gap: 0;
  }
  .help-text {
    font-size: 16px;
    width: auto;
  }

  .btn-help {
    width: 180px;
  }
}

@media (max-width: 426px) {
  .heading {
    font-size: 24px;
    text-align: left;
    width: auto;
  }
  .subheading {
    font-size: 14px;
  }
}

@media (max-width: 350px) {
  .card {
    width: 290px;
  }
}
