.main-nav {
  width: 100%;
  max-width: 1360px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  height: 74px;
  padding: 0 40px;
}

.select-order {
  height: 44px;
  width: 167px;
  padding-left: 24px;
  margin-left: 30px;
  border: 1px solid #7238ed;
  border-radius: 4px;

  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-image: none;
}
.select-language {
  height: 44px;
  // flex: 0 1 128px;
  width: 128px;

  padding-left: 42px;
  border: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-image: none;
}

.search,
.select-order-wrapper,
.select-language-wrapper {
  position: relative;
  // flex-shrink: 1;
}

.search {
  flex: 0 1 335px;
  margin-left: 16px;
}

.search-input {
  box-sizing: border-box;
  height: 44px;
  // width: 335px;
  width: 100%;
  padding: 14px 52px;
  padding-right: 12px;
  font-size: 16px;
  border: 1px solid #f1f1f1;
  font-family: inherit;
  border-radius: 4px;
  &::placeholder {
    color: #a8a8a8;
  }
}
.search-icon {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
}

.arrow {
  position: absolute;
  right: 24px;
  top: 20px;
}

.btn-find {
  background-color: #7238ed;
  color: #fff;
  height: 44px;
  width: 165px;
  min-width: 100px;
  // flex: 0 0 165px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  font-size: 16px;
  font-family: inherit;
}
.btn-sign-in {
  background-color: #000;
  color: #fff;
  height: 44px;
  width: 113px;
  min-width: 50px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-right: 16px;
}
.btn-become-expert {
  background-color: #f2f2f2;
  color: #000;
  height: 44px;
  width: 193px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

@media (max-width: 1202px) {
  .btn-become-expert,
  .btn-find,
  .btn-sign-in {
    font-size: 14px;
  }
}

.suggestions {
  width: 335px;
  position: absolute;
  top: 45px;
  left: 0;
  display: flex;
  flex-direction: column;
  background-color: rgba(204, 204, 204, 0.425);
  z-index: 20;
}
.suggestion {
  display: flex;
  align-items: center;
  height: 40px;
  width: 100%;
  padding-left: 12px;
  cursor: pointer;
  &:hover {
    background-color: rgba(0, 0, 0, 0.61);
    color: #fff;
  }
}

@media (max-width: 1138px) {
  .search-input {
    &::placeholder {
      color: #fff;
    }
  }
  .select-language {
    padding-left: 8px;
    width: 80px;
  }
}

@media (max-width: 1035px) {
  .btn-become-expert {
    display: none;
  }
}

@media (max-width: 700px) {
  .main-nav {
    padding: 0 24px;
  }
  .search,
  .btn-find,
  .btn-sign-in,
  .btn-become-expert,
  .select-language-wrapper {
    display: none;
  }

  .select-order {
    width: 116px;

    padding-left: 16px;
    margin-right: 16px;
    margin-left: 16px;
  }
  .arrow {
    // right: 0;
  }

  .mobile-icons {
    margin-left: auto;
    display: flex;
    align-items: center;
    gap: 16px;
  }
}

@media (max-width: 370px) {
  .main-nav {
    padding: 0 6px;
  }

  .select-order {
    width: 100px;

    padding-left: 6px;
    margin-right: 6px;
    margin-left: 6px;
  }
  .arrow {
    right: 10px;
  }
}
