.wrapper {
  z-index: 50;
  // position: absolute;
  // bottom: 0;
  width: 100%;
  max-width: 730px;
  min-width: 550px;
  background-color: transparent;
}

.top {
  display: flex;
  background-color: #fff;
  box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.04);
  // width: 100%;
}

.inputs-wrapper {
  // width: 527px;
  flex-grow: 1;
  height: 80px;
  padding-left: 36px;
  box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.04);
  border-radius: 4px;
  display: flex;
  align-items: center;
  gap: 40px;
}

.input-1-wrapper,
.input-2-wrapper {
  position: relative;
  height: 40px;
}

.input-1 {
  border: none;

  height: 100%;
  width: 244px;
  padding-left: 32px;
  padding-right: 20px;
  font-family: inherit;
  font-size: 18px;
  &::placeholder {
    color: #a8a8a8;
  }
  &:focus {
    outline: none;
  }
}
.input-2 {
  border: none;
  height: 100%;
  width: 140px;
  padding-left: 34px;
  font-family: inherit;
  font-size: 18px;
  &::placeholder {
    color: #a8a8a8;
  }
  &:focus {
    outline: none;
  }
}

.search-icon {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.point-icon {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.separator {
  width: 1px;
  height: 56px;
  background-color: #ccc;
}
.btn-find {
  background-color: #7238ed;
  color: #fff;
  height: 80px;
  width: 211px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  font-size: 20px;
  font-family: inherit;
}
.footer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  line-height: 23px;
  margin-top: 20px;
}

.link {
  color: #7238ed;
  text-decoration: none;
}

.new-users {
  color: #a8a8a8;
}

@media (max-width: 1100px) {
  // .wrapper {
  //   width: calc(100vw - 97px);
  // }
  .input-2-wrapper,
  .separator {
    display: none;
  }

  .wrapper {
    min-width: 280px;
    max-width: 500px;
  }
}

@media (max-width: 600px) {
  .wrapper {
    max-width: none;
    background-color: transparent;
  }
  .top {
    flex-direction: column;
    gap: 12px;
    background-color: transparent;
  }

  .inputs-wrapper {
    height: 56px;
    border-radius: 8px;
    width: 100%;
    background-color: #fff;
  }
  .input-1-wrapper {
    width: 100%;
  }
  .input-1 {
    border-radius: 8px;
    width: 100%;
  }

  .btn-find {
    height: 56px;
    width: 100%;
    border-radius: 8px;
  }

  .link {
    font-size: 16px;
  }
  .new-users {
    font-size: 16px;
  }
}
