.section-footer {
  width: 100%;
  max-width: 1440px;
  padding: 108px 80px;
  padding-bottom: 0;
  margin: 0 auto;
}

.content {
  display: flex;
}

.contacts {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.logo {
  margin-bottom: 119px;
  width: 128px;
}

.social {
  display: flex;
  gap: 8px;
  margin-bottom: 20px;
}

.email {
  font-weight: 500;
}

.columns {
  display: flex;
  gap: 90px;
}

.col-name {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 20px;
}

.links {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.link {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.7);
}

.footer {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  color: rgba(0, 0, 0, 0.7);
  height: 64px;
  display: flex;
  align-items: center;
  margin-top: 57px;
}
