.card {
  padding: 32px;
  width: 380px;
  height: 468px;
  border-radius: 8px;
  box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.04);
}

.user {
  display: flex;
  align-items: center;
  gap: 20px;
  height: 64px;
  margin-bottom: 24px;
}

.user-info {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.user-name {
  font-size: 20px;
  font-weight: 500;
}

.user-position {
  color: rgba(0, 0, 0, 0.5);
}

.heading {
  font-size: 24px;
  font-weight: 500;
  line-height: 26px;
  margin-bottom: 16px;
}

.message {
  color: #8f8f8f;
  line-height: 26px;
  margin-bottom: 42px;
}

.stars-block {
  display: flex;
  gap: 16px;
}

.stars {
  display: flex;
  gap: 10px;
}

.rating {
  font-weight: 500;
}
