.page {
  padding-bottom: 100px;
  padding-top: 85px;
}

.hero {
  height: 500px;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding-top: 105px;
  padding-left: 56px;
  padding-right: 20px;
  border-radius: 8px;

  //   background-image: url("../../icons/HPHero/bg.png");
  background-color: #77a7ff;
  background: linear-gradient(to right, #77a7ff, #4441d5);
  position: relative;
  // margin-bottom: 50px;
}

.image {
  // position: relative;
  // margin-left: 40%;
  // width: 100px;
  // height: 100px;
}

.left {
  width: 40%;
  // display: flex;
  // flex-direction: column;
}
.heading {
  color: #fff;
  font-size: 48px;
  line-height: 58px;
  margin-bottom: 16px;
  font-family: "Rubik";
  font-weight: 600;
}
.subheading {
  color: #fff;
  font-size: 16px;
  line-height: 26px;
  margin-bottom: 40px;
}

.btn-submit {
  background-color: #4441d5;
  color: #fff;
  font-size: 16px;
  font-family: inherit;
  width: 195px;
  height: 49px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.plumber {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 80vw;
  max-width: 953px;
  min-width: 700px;
  // width: 673px;
}
.sphere {
  position: absolute;
}
.arc {
  position: absolute;
  left: -105px;
  top: 20px;
  transform: rotate(-15deg);
}

@media (max-width: 1032px) {
  .hero {
    padding-top: 70px;
  }
  .heading {
    font-size: 40px;
    line-height: 46px;
  }
}

@media (max-width: 858px) {
  .plumber {
    right: -50px;
  }

  .hero {
    overflow: hidden;
  }
}

@media (max-width: 790px) {
  .hero {
    display: flex;
    flex-direction: column;
    height: fit-content;
  }

  .left {
    width: 100%;
  }
  .heading {
    width: 100%;
  }
  .subheading {
    width: 100%;
  }

  .plumber {
    position: static;
  }
}

@media (max-width: 671px) {
  .plumber {
    position: relative;
    left: -200px;
  }
}
@media (max-width: 453px) {
  .hero {
    padding-top: 24px;
    padding-left: 24px;
  }
  .heading {
    font-size: 24px;
    line-height: 32px;
  }
  .subheading {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 16px;
  }

  .btn-submit {
    width: 180px;
  }

  .plumber {
    min-width: 475px;
    // left: -230px;
    left: -150px;
  }
}
